const About = () => {
  return (
    <section id="about" className="lowered">
      <div className="wrapper">
        <h2>About</h2>
        <article className="about-upper">
          <figure className="about-left">
            <div className="decoration-bar" />
            <img src={aboutImg} alt="Our team" />
            <div className="decoration-bar" />
          </figure>
          <div className="about-right">
            <p>
              We are small company made by three life-time bartenders.
              All of us worked at some of the best clubs in UK, in 2018
              we decided to create our own brand and serve people our best
              cocktails. We love it pretty much, meet our team in !Thirsty!  
            </p>
          </div>
        </article>
        <article className="about-lower">
          <div className="container">
            <div className="team-member-card">
              <figure className="team-member-avatar">{mike}</figure>
              <figcaption className="team-member-name">Mike</figcaption>
            </div>
            <span className="characters">+</span>
            <div className="team-member-card">
              <figure className="team-member-avatar">{kyle}</figure>
              <figcaption className="team-member-name">Kyle</figcaption>
            </div>
            <span className="characters">+</span>
            <div className="team-member-card">
              <figure className="team-member-avatar">{josh}</figure>
              <figcaption className="team-member-name">Josh</figcaption>
            </div>
          </div>
          <span className="characters">=</span>
          <div className="logo-longer-container"><img className="logo-longer" src={logo} alt="!Thirsty team" /><span className="logo-follow">!Thirsty Team</span></div>
        </article>
      </div>
    </section>
  )
}

export default About